* {
  box-sizing: border-box; }

html, body {
  width: 100%;
  height: 100%; }

body {
  line-height: 1.42;
  -webkit-font-smoothing: antialiased; }

.container {
  width: 940px;
  margin: 0 auto; }

.wrapper {
  overflow: hidden;
  min-height: 100%;
  min-width: 960px; }
  .wrapper:after {
    content: "";
    display: block;
    height: 50px; }

.footer {
  height: 50px;
  margin-top: -50px;
  min-width: 960px; }

ul {
  padding: 0;
  margin: 0; }

ul li {
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal; }

.clearfix:after {
  content: "";
  display: block;
  clear: both; }
